.services-benefits {
  padding-top: var(--offset-section-middle);
  background-color: var(--color-gray-900);

  &__descr {
    margin-bottom: var(--offset-descr);
    color: rgba(255, 255, 255, 0.6);
    font-size: 18px;
    line-height: 150%;
  }

  &__list {
    margin-bottom: 100px;
    --mission-gap: 94px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--mission-gap) * 2);

    @include tablet {
      --mission-gap: 30px;
    }

    @include minitablet {
      --mission-gap: 10px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    position: relative;
    text-align: center;
    padding-top: 72px;
    background-position: center top;
    background-repeat: no-repeat;
    background-size: 48px 48px;

    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        right: calc(-1 * var(--mission-gap));
        top: 0;
        width: 1px;
        height: 100%;
        background: linear-gradient(315deg, rgba(218, 219, 221, 0.00) 0%, rgba(218, 219, 221, 0.50) 12.33%, #DADBDD 51.91%, rgba(218, 219, 221, 0.50) 87.85%, rgba(218, 219, 221, 0.00) 100%);

        @include mobile {
          display: none;
        }
      }
    }

    &--quality {
      background-image: url("../img/like.svg");
    }

    &--safety {
      background-image: url("../img/hand.svg");
    }

    &--comfort {
      background-image: url("../img/slippers.svg");
    }
  }

  &__sub-title {
    display: block;
    margin-bottom: 8px;
    font-size: 20px;
    font-weight: 700;
    line-height: 150%;
    color: var(--color-light);
  }

  &__text {
    line-height: 160%;
    color: rgba(255, 255, 255, 0.6);
  }

  &__centered {
    margin-bottom: -26px;
  }

  &__btn {
    min-width: 390px;
    transform: translateY(26px);

    @include mobile {
min-width: 290px;
    }
  }
}
