.pricing {
  padding-top: var(--offset-section-middle);
  padding-bottom: 40px;
  background-color: var(--color-gray-200);

  &__descr {
    margin-bottom: var(--offset-descr);
  }

  .btn {
    @include tablet {
      padding: 0 10px;
    }
  }

}


.table-wrapper {
  position: relative;
  z-index: 10;
  overflow: hidden;
  width: 100%;
  border-radius: 4px;
  border: 1px solid var(--color-gray-300);
  background-color: var(--color-light);
  box-shadow:
    0px 4px 4px -4px rgba(30, 33, 44, 0.03),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 80px 80px -20px rgba(154, 156, 165, 0.08);

    @include minitablet {
     overflow-x: auto;
}
}

.table {
  border-collapse: collapse;
width: 100%;

@include minitablet {
width: 900px;
}

&__header {

}

&__heading {
  color: var(--color-gray-900);
font-size: 24px;
font-weight: 700;
line-height: 150%;
}

&__per {
  color: var(--color-gray-700);
font-size: 18px;
font-weight: 700;
line-height: 150%;
}

&__body {
  .table__row {
    &:nth-child(odd) {
      background-color: var(--color-gray-200);
    }

    &:last-child {
      .table__col {
        padding-top: 32px;
        padding-bottom: 32px;
      }
    }
  }
}

&__th {
  padding-top: 32px;
  padding-bottom: 24px;
  &:first-child {
    padding-left: 24px;
    text-align: left;
    vertical-align: bottom;
    color: var(--color-gray-900);
font-size: 18px;
font-weight: 700;
line-height: 150%;
  }
}

&__col {
  padding-top: 16px;
  padding-bottom: 16px;
  text-align: center;
  vertical-align: middle;
  color: var(--color-gray-800);
line-height: 160%;

  &:first-child {
    text-align: left;
    padding-left: 24px;
  }
}


&__check {
  font-size: 0;
  display: inline-block;
  width: 24px;
  height: 24px;
  background-image: url("../img/table-check.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  vertical-align: middle;
}

}
