.resent-news {
  padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);
  background-color: var(--color-light);

  &__title {
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 20px;
    }

  }

  &__list {
    margin-bottom: 80px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 255px);
    gap: 30px;

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(2, auto);
      margin-bottom: 20px;
    }
  }

  &__item {
    grid-column: 5 span;
    @include tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }

    &:first-child {
      grid-column: 7 span;
      grid-row: 2 span;

      @include tablet {
        grid-column: 12 span;
      }

      @include mobile {
        grid-column: 4 span;
      }

    }
  }
}

