.achieve {
  padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);


  &__list {
    margin-bottom: 240px;

    @include minidesk {
     margin-bottom: 60px;
    }

    @include mobile {
      gap: 10px;
      row-gap: 20px;
     }
  }

  &__item {
    padding-top: 68px;
    position: relative;
    grid-column: 3 span;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    background-size: 48px 48px;
    background-position: top center;
    background-repeat: no-repeat;

    @include minitablet {
      grid-column: 6 span;
     }

     @include mobile {
      grid-column: 12 span;
     }

    &--reccom {
      background-image: url("../img/reccom.svg");
    }

    &--renov {
      background-image: url("../img/renov.svg");
    }

    &--spec {
      background-image: url("../img/spec.svg");
    }

    &--finish {
      background-image: url("../img/finish.svg");
    }
  }

  &__value {

  }

  &__text {
    text-align: center;
    color: var(--color-gray-900);
    font-size: 14px;
    line-height: 150%;
  }

  &__content {
    display: flex;
    align-items: flex-start;

    @include minitablet {
      display: block;
    }


    @include minitablet {
      text-align: center;
    }
  }

  &__image {
    @include minidesk {
    max-width: 45%;
    }

    @include minitablet {
      max-width: initial;
      max-height: 400px;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__left {}

  &__info {
    padding: 80px;
    position: relative;
    top: -120px;
    left: -30px;
    z-index: 10;
    width: 735px;
    flex-shrink: 0;
    border-radius: 4px;
    background: var(--color-light);
    box-shadow:
      0px 4px 4px -4px rgba(30, 33, 44, 0.03),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04),
      0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 80px 80px -20px rgba(154, 156, 165, 0.08);

      @include minidesk {
       width: 55%;
       padding: 50px 30px;
       left: 0;
       top: 0;
      }


    @include minitablet {
   width: 100%;
    }

    @include mobile {
     padding: 20px 0;
    }
  }

  &__quote {}
}

.quote {

  @include mobile {
   margin: 0;
  }

  &__content {
    position: relative;
    margin-bottom: var(--offset-descr);
    color: var(--color-gray-900);
    font-size: 20px;
    font-weight: 700;
    line-height: 150%;

    @include minitablet {
      font-size: 16px;
    }

    &::before {
      position: absolute;
      top: -5px;
      left: -60px;
      content: "";
      width: 28px;
      height: 20px;
      background-image: url("../img/braces.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      @include mobile {
       display: none;
      }
    }
  }

  &__descr {
    margin-bottom: 30px;
  }

  &__footer {
    position: relative;
    color: var(--color-gray-900);
    font-weight: 700;
    line-height: 160%;

    &::before {
      position: absolute;
      top: 0;
      right: 0;
      content: "";
      width: 100px;
      height: 68px;
      background-image: url("../img/signature.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      @include tablet {
        display: none;
      }
    }
  }

  &__author {
    display: block;
  }
}
