.contacts-section {
  padding-top: var(--offset-section-middle);
  padding-bottom: var(--offset-section-middle);
  background-color: var(--color-gray-800);
  background-image: url("../img/contacts-section-bg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  &__container {
    display: flex;
    justify-content: flex-end;
  }

  &__form {
    background-color: var(--color-light);
  }
}

.contacts-form {
  max-width: 495px;
  padding: 48px 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  border-radius: 4px;
  border: 1px solid var(--color-gray-300);
  box-shadow:
  0px 4px 4px -4px rgba(30, 33, 44, 0.05),
  0px 12px 10px -6px rgba(30, 33, 44, 0.08),
  0px 26px 24px -10px rgba(30, 33, 44, 0.10),
  0px 60px 80px -20px rgba(30, 33, 44, 0.16);

  @include mobile {
    padding: 15px;
  }

  &__title {
    margin-bottom: 4px;
  }

  &__field {
    width: 100%;
  }

  &__btn{

  }
}
