.how-we-do {
  padding-top: var(--offset-section-small);
  padding-bottom: var(--offset-section-big);


  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    counter-reset: section;

    @include mobile {
      display: block;
    }
  }

  &__item {
    position: relative;
    grid-column: 3 span;

    @include minitablet {
      grid-column: 6 span;
    }


    &::before {
      display: inline-block;
      margin-bottom: 16px;
      counter-increment: section;
      content: "0"counter(section);
      color: var(--color-gray-400);
      font-size: 46px;
      font-weight: 700;
      line-height: 130%;
    }

    &:not(:last-child) {
      &::after {
        position: absolute;
        content: "";
        right: 0;
        top: 30px;
        width: 70%;
        height: 1px;
        background-image: url(../img/how-we-do-line.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

        @include tablet {
          display: none;
        }
      }
    }


  }

  &__subtitle {
    margin-bottom: 8px;
  }

}
