.project-goal {
  padding-top: 20px;
  padding-bottom: var(--offset-section-big);

  .grid {
    @include minitablet {
      display: block;
    }
  }

  &__left {
    grid-column: 6 span;
  }

  &__right {
    grid-column: 6 span;
    display: flex;
    justify-content: flex-end;
    @include minitablet {
      justify-content: center;
    }
  }

  &__descr {
    margin-bottom: 20px;
  }
}

.project-info {
  padding: 40px 60px;
  border-radius: 4px;
background-color: var(--color-light);
box-shadow:
    0px 4px 4px -4px rgba(30, 33, 44, 0.03),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 80px 80px -20px rgba(154, 156, 165, 0.08);

    @include tablet {
      padding: 20px;
    }

    @include mobile {
      padding: 10px 0;
    }

    &__list {
      display: flex;
      flex-direction: column;
      gap: 16px;
    }

    &__item {
      display: flex;
    }

    &__category {
      flex-shrink: 0;
      width: 130px;
      color: var(--color-gray-900);
font-weight: 700;
line-height: 160%;
    }
}
