.form-field {
  display: flex;
  flex-direction: column;

  &__caption {
    margin-bottom: 7px;
    color: var(--color-gray-800);
    font-size: 14px;
    line-height: 150%;
  }

  &__input {
    padding: 12px 16px;
    width: 260px;
    height: 44px;
    border-radius: 4px;
    border: 1px solid var(--color-gray-400);
    background: var(--color-gray-200);
    color: var(--color-gray-600);
    font-size: 14px;
    line-height: 150%;

    @include middledesk {
      width: 200px;
    }

    &--big {
      width: 414px;

      @include middledesk {
        width: 290px;
      }
    }

    &::placeholder {
      color: var(--color-gray-600);
    }

    &:focus {
      outline: 1px solid var(--color-primary);
      outline-offset: 2px;
      }
  }

  &__input--length {
width: 100%;
  }

  &__input--height {
height: 66px;
resize: none;
  }


&--tex {
  width: 100%;
}
}
