.post-form {
  padding-top: 40px;
  padding-bottom: 40px;

&__title {
  margin-bottom: 48px;
}

&__content {
display: flex;
flex-wrap: wrap;
justify-content: space-between;

@include mobile {
 gap: 20px;
}
}

&__field {
width: calc((100% - 30px) / 2);

@include mobile {
  width: 100%;
}

&--tex {
  margin-top: 22px;
  margin-bottom: 48px;
  width: 100%;


@include mobile {
margin-top: 0;
}
}
}

&__input {
resize: none;
height: 100px;
}

&__btn {

}

}
