.positions-content {
  padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);


  &__info {
    @include mobile {
      gap: 10px;
    }
  }

  &__left {
    grid-column: 7 span;

    @include mobile {
      grid-column: 12 span;
    }
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    grid-column: 5 span;

    @include mobile {
      grid-column: 12 span;
      justify-content: flex-start;
    }
  }




}



.positions-list {
  display: flex;
  flex-direction: column;
  gap: 24px;

  &__item {}
}

.positions-item {
  padding: 32px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
  border: 1px solid var(--color-gray-300);
  background: var(--color-light);

  @include tablet {
    padding: 20px;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }




  &__left {}

  &__meta {
    margin-bottom: 8px;
    color: var(--color-gray-700);
    font-size: 14px;
    line-height: 150%;
  }

  &__city {
    position: relative;
    margin-right: 24px;
    padding-left: 24px;
    background-image: url("../img/city.svg");
    background-position: left center;
    background-size: 16px 16px;
    background-repeat: no-repeat;

    &::after {
      position: absolute;
      right: -12px;
      top: 50%;
      transform: translateY(-50%);
      content: "";
      height: 11px;
      width: 1px;
      background-color: currentColor;

    }
  }

  &__time {
    padding-left: 24px;
    background-image: url("../img/time.svg");
    background-position: left center;
    background-size: 16px 16px;
    background-repeat: no-repeat;
  }

  &__title {
    color: var(--color-gray-900);
    font-size: 20px;
    font-weight: 700;
    line-height: 150%;
  }

  &__linc {}

}

.positions-looking {
  width: 380px;



  &__title {
    margin-bottom: 24px;
  }

  &__descr {
    margin-bottom: 48px;
    color: var(--color-gray-800);
    font-size: 18px;
    line-height: 150%;
  }

  &__btns {
    display: flex;
    justify-content: space-between;

    @include tablet {
    flex-direction: column;
    gap: 20px;
    }
  }

  &__btn {}
}
