.testimonials {
  padding-top: 72px;
  padding-bottom: 20px;
  // padding-bottom: var(--offset-section-big);
  background: var(--color-gray-200);

  @include mobile {
   padding-top: var(--offset-section-big);
  }

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__content {
    max-width: 600px;

    @include tablet {
      max-width: 400px;
    }

    @include minitablet {
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 60px;
  }

  &__items {
    position: relative;
  }

  &__slider-nav {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  &__image {
    position: relative;
    z-index: 10;
    margin-bottom: -60px;
    border-radius: 4px;
    object-fit: cover;

    @include minidesk {
      width: 350px;
    }

    @include tablet {
      width: 300px;
    }

    @include minitablet {
      display: none;
    }


  }



}

.testimonials-item {
  &__image {
    margin-bottom: 24px;
    overflow: hidden;
    border-radius: 100%;
    width: 72px;
    height: 72px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__descr {
    margin-bottom: 24px;
  }

  &__author {
    display: block;
    margin-bottom: 4px;
    color: var(--color-gray-900);
    font-weight: 700;
    line-height: 160%;
  }

  &__author-post {}
}
