.history {
  padding-top: var(--offset-section-middle);
  padding-bottom: 100px;
  background: var(--color-gray-200);

  &__top {
    margin-bottom: var(--offset-descr);
display: flex;
justify-content: space-between;
align-items: flex-end;
  }



  &__title {
margin-bottom: 0;
  }

  &__slider-nav {

  }

  &__content {
    @include mobile {
      display: block;
    }
  }

  &__item {
    overflow: hidden;
    grid-column: 8 span;
  }

  &__image {
margin-bottom: 36px;
  }

  &__descr {
    color: var(--color-gray-800);
    line-height: 160%;
  }
}

.history-nav {
  position: relative;
  grid-column: 4 span;

  @include mobile {
 margin-bottom: 20px;
  }

  &__item {

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &::before {
      position: absolute;
      left: 3px;
      top: 0;
      content: "";
      width: 1px;
      height: 100%;
      background-color: rgba($color: #9A9CA5, $alpha: 0.5);

    }

  }

  &__btn {
    padding-left: 40px;
    position: relative;
color: var(--color-gray-600);
font-size: 28px;
font-weight: 700;
line-height: 150%;
transition: color .3s ease-in-out;

@include tablet {
  font-size: 22px;
}


@include minitablet {
  padding-left: 20px;
  font-size: 18px;
}

&::before {
  position: absolute;
  z-index: 10;
  left: 3px;
  top: 13px;
  content: "";
  width: 1px;
  height: 16px;
  background-color: var(--color-gray-200);

}

&::after {
  position: absolute;
  z-index: 10;
  left: 0;
  top: 17px;
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 100%;
  background-color:currentColor;
}

&:focus {
  color: var(--color-primary);
  outline: none;
}

&:hover {
  color: var(--color-primary);
}

&--active {
  color: var(--color-primary);
}

  }
}


