.portfolio {
  position: relative;
  padding-bottom: 100px;
}

.portfolio-tabs-nav {
  margin-bottom: 60px;
  --gap: 20px;
  display: flex;
  align-items: center;
  gap: var(--gap);
  transform: translateY(-60px);

  @include minitablet {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    transform: none;
  }

  &__item {
    width: calc((100% - (var(--gap) * 4)) / 5);

    @include minitablet {
      width: calc((100% - (var(--gap) * 2)) / 3);
  }
  @include minitablet {
    width: 80%;
}
}

  &__btn {
    padding: 2px;
    min-height: 154px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    border-radius: 4px;
    color: var(--color-primary);
    background: var(--color-light);
    box-shadow:
      0px 4px 4px -4px rgba(30, 33, 44, 0.03),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04),
      0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 80px 80px -20px rgba(154, 156, 165, 0.08);
      transition: color .3s ease-in-out, background-color .3s ease-in-out;

      &:hover {
        .portfolio-tabs-nav__text {
          color: var(--color-gray-800);
        }
      }

      &--active {
        color: var(--color-light);
        background-color: var(--color-primary);
        pointer-events: none;

        .portfolio-tabs-nav__text {
          color: var(--color-light);
        }
      }
  }

  &__icon {
    display: block;
    margin-bottom: 16px;
    width: 48px;
    height: 48px;
    object-fit: cover;
    color: inherit;
    fill: currentColor;
    pointer-events: none;
  }

  &__text {
    color: var(--color-gray-600);
text-align: center;
font-weight: 700;
line-height: 160%;
pointer-events: none;
transition: color .3s ease-in-out;
  }

}

.portfolio-tabs {

  @include mobile {
  gap: 10px;
  }

  &__item {
    display: none;
    grid-column: 4 span;
    grid-row: auto;

    @include minitablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }

    &--vissible {
      display: block;
    }
  }
}

.portfolio-more {
  margin-top: 60px;
  display: inline-flex;
  align-items: center;
  color: var(--color-gray-800);
font-weight: 700;
line-height: 160%;

svg {
  margin-right: 16px;
  width: 24px;
  height: 24px;
  color: inherit;
  fill: currentColor;
  object-fit: cover;
}
}
