.team {
  padding-top: var(--offset-section-middle);
  padding-bottom: var(--offset-section-big);

  &__title {}

  &__descr {
    margin-bottom: var(--offset-descr);
  }

  &__list {
    margin-bottom: 80px;
    row-gap: 60px;

    @include mobile {
     gap: 10px;
      }
  }

  &__item {
    grid-column: 3 span;

    @include tablet {
      grid-column: 6 span;
    }

    @include mobile {
     grid-column: 12 span;
    }

    &:hover {
      .team__socials {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__top {
    position: relative;
        margin-bottom: 16px;
        font-size: 0;

        @include tablet {
          text-align: center;
        }
  }

    &__socials {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
    text-align: center;
    padding: 20px;
    border-radius: 0px 0px 4px 4px;
    opacity: 0.7;
    background: var(--color-primary);
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

    @include mobile {
      display: none;
    }
    }

  &__image {
    border-radius: 4px;
  }

  &__name {
    margin-bottom: 4px;
    text-align: center;
    color: var(--color-gray-800);

    font-size: 20px;

    font-weight: 700;
    line-height: 150%;
  }

  &__post {
    text-align: center;
    color: var(--color-gray-700);
    line-height: 160%;
  }

  &__link {

    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__text {
    color: var(--color-gray-900);
    font-size: 18px;
    line-height: 150%;

    a {
      color: var(--color-primary);
      border-bottom: 1px solid currentColor;
    }
  }
}
