html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  font-size: 0;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

a {
  text-decoration: none;
  transition: color .3s ease-in-out;

  &:focus {
    outline: 1px solid var(--color-primary);
    outline-offset: 2px;
  }

  &:hover {
    color: var(--color-primary);
  }

}

// .site-container {
//   overflow: hidden; //     position: sticky -   
// }

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}


.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

//////////////////////////////////
.page {
  height: 100%;
  font-family: var(--family-main);
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.main-title {
  margin-bottom: 24px;
  font-size: 72px;
  font-weight: 700;
  line-height: 130%;
  letter-spacing: 1px;
  color: var(--color-gray-900);

  span {
    color: var(--color-primary);
  }

  &--sm {
    color: var(--color-gray-900);
    font-size: 64px;
    font-weight: 700;
    line-height: 120%;
    letter-spacing: 1px;
  }

  @include minitablet {
    font-size: 60px;
  }

  @include mobile {
    font-size: 35px;
  }
}

.second-title {
  margin-bottom: 24px;
  font-size: 46px;
  font-weight: 700;
  line-height: 130%;
  color: var(--color-gray-900);

  @include tablet {
    font-size: 40px;
  }

  @include tablet {
    font-size: 28px;
  }

  &--white {
    margin-bottom: 24px;
    font-size: 46px;
    font-weight: 700;
    line-height: 130%;
    color: var(--color-light);

    @include tablet {
      font-size: 40px;
    }

    @include tablet {
      font-size: 28px;
    }
  }

  &--primary {
    color: var(--color-primary);
    font-size: 46px;
    font-weight: 700;
    line-height: 130%;

    @include tablet {
      font-size: 40px;
    }

    @include tablet {
      font-size: 28px;
    }
  }
}

.third-title {
  color: var(--color-gray-900);
  font-size: 28px;
  font-weight: 700;
  line-height: 150%;

  @include tablet {
    font-size: 24px;
  }
}

.sub-title {
  color: var(--color-gray-900);
  font-size: 20px;
  font-weight: 700;
  line-height: 150%;

  @include mobile {
    font-size: 16px;
  }
}



.main-descr {
  font-size: 20px;
  line-height: 150%;
  color: var(--color-gray-700);

  @include tablet {
    font-size: 18px;
  }
}

.second-descr {
  font-size: 18px;
  line-height: 150%;
  color: var(--color-gray-700);

  @include tablet {
    font-size: 16px;
  }
}

.third-descr {
  color: var(--color-gray-700);
  font-size: 16px;
  line-height: 160%;

  @include tablet {
    font-size: 14px;
  }
}

.fourth-descr {
  color: var(--color-gray-700);
  font-size: 14px;
  line-height: 150%;

  @include tablet {
    font-size: 12px;
  }
}

.text-center {
  text-align: center;
}

.centered {
  width: 100%;
  text-align: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}

////////////////////////////
.custom-checkbox {
  position: relative;

  &__field {
    position: absolute;
    opacity: 0;
  }

  &__content {
    position: relative;
    display: inline-block;
    padding-left: 28px;
    color: var(--color-gray-800);
    font-size: 14px;
    line-height: 150%;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 2px;
      display: inline-block;
      border: 1px solid var(--color-primary);
      width: 16px;
      height: 16px;
      border-radius: 3px;
      transition: background-color 0.3s ease-in-out;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 2px;
      width: 16px;
      height: 16px;
      background-image: url("../img/checked.svg");
      background-position: center;
      background-size: 10px 8px;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }
}

.custom-checkbox__field:checked+.custom-checkbox__content::before {
  background-color: var(--color-primary);
}

.custom-checkbox__field:checked+.custom-checkbox__content::after {
  opacity: 1;
}


.custom-radio {
  position: relative;

  &__field {
    position: absolute;
    opacity: 0;
  }

  &__content {
    position: relative;
    display: inline-block;
    padding-left: 28px;
    color: var(--color-gray-800);
    font-size: 14px;
    line-height: 160%;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 2px;
      display: inline-block;
      border: 1px solid var(--color-gray-400);
      width: 16px;
      height: 16px;
      border-radius: 100px;
      transition: background-color 0.3s ease-in-out;
    }

    &::after {
      content: "";
      position: absolute;
      left: 4px;
      top: 6px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      width: 8px;
      height: 8px;
      border-radius: 100px;
      background-color: var(--color-primary);


    }
  }


  &__field:checked+&__content::after {
    opacity: 1;
  }

  &__field:checked+&__content::before {
    border-color: var(--color-primary);
  }
}


.to-top {
  position: fixed;
  z-index: 1000;
  bottom: 80px;
  right: calc((100% - 1230px)/ 2);
  display: inline-flex;
  align-items: flex-end;
  gap: 16px;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;


  @include middledesk {
    bottom: 20px;
    right: 20px;
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }

  &__text {
    color: var(--color-light);
    font-size: 14px;
    line-height: 150%;
  }


  &__icon {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    background-color: var(--color-primary);
    transition: background-color .3s ease-in-out;


    svg {
      width: 24px;
      height: 24px;
      object-fit: cover;
      fill: var(--color-light);
    }

    &:hover {
      background-color: var(--color-active);
    }

  }
}


.site-modal {
  padding: 48px;
  width: 486px;

  &__title {
    margin-bottom: 24px;
    color: var(--color-gray-900);
    text-align: center;
    font-size: 28px;
    font-weight: 700;
    line-height: 150%;
  }

  &__field {
    input {
      width: 100%;
    }


    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__btn {
    width: 100%;
    margin-top: 40px;
  }
}


.overlay {
  position: fixed;
  inset: 0;
  z-index: 400;
  background-color: rgba(#000, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

  &--active {
    opacity: 1;
    visibility: visible;
  }
}

.site-modal {
  @include mobile {
    padding: 10px;
    width: 90%;
  }
}
