.header {
  padding-top: 22px;
  padding-bottom: 22px;

  &--main {
    background-color: var(--color-light);
  }

  &--secondary {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__nav {
    margin-left: -100px;

    @include minidesk {
      margin-left: 0;
    }

    @include tablet {
      display: block;
      position: fixed;
      z-index: 500;
      overflow-y: auto;
      left: 0;
      top: 0;
      width: 80%;
      height: 100vh;
      padding: 50px;
      background-color: var(--color-light);
      transform: translateX(-150%);
      transition: transform .3s ease-in-out;

      &--active {
        transform: none;
      }
    }

    @include mobile {
      padding: 20px;
    }
  }
}

.logo {
  font-size: 0;

  @include minidesk {
    max-width: 100px;
  }
}
