.btn {
  display: inline-block;
  border-radius: 4px;
  text-align: center;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  transition: background-color .3s ease-out, color .3s ease-out;

  &--large {
    padding-left: 40px;
    padding-right: 40px;
    font-size: 16px;
    line-height: 52px;

    @include minitablet {
      font-size: 14px;
    }
  }

  &--regular {
    padding-left: 32px;
    padding-right: 32px;
    font-size: 14px;
    line-height: 44px;
  }

  &--small {
    padding-left: 24px;
    padding-right: 24px;
    font-size: 12px;
    line-height: 36px;
  }

  &--solid {
    color: var(--color-light);
    background-color: var(--color-primary);

    &:focus {
      outline: 1px solid var(--color-primary);
      outline-offset: 3px;
      }

      &:hover {
        background-color: var(--color-active);
      }

      &:active {
        background-color: var(--color-active);
      }

  }

  &--outline {
    outline: 1px solid var(--color-primary);
    color: var(--color-primary);
    background-color: transparent;

    &:focus {
      outline: 1px solid var(--color-primary);
      outline-offset: 3px;
      }

      &:hover {
        background-color: var(--color-active);
        outline-color: var(--color-active);
        color: var(--color-light);
      }

      &:active {
        background-color: var(--color-active);
        color: var(--color-light);
      }
  }




}
