.clientts {
  padding-top: 180px;
  padding-bottom: var(--offset-section-middle);
  background-color: var(--color-light);

  @include mobile {
    padding-top: 40px;
  }


  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    row-gap: 20px;
    column-gap: 102px;


  @include mobile {
    column-gap: 20px;
  }

  }


  &__item {
    @include tablet {
      grid-column: 2 span;
    }
  }
}
