.breadcrumbs {
display: flex;
align-items: center;

@include mobile {
  flex-wrap: wrap;
}

&__item {
  position: relative;

&:not(:last-child) {
  margin-right: 22px;

  &::after {
    position: absolute;
    content: "/";
    right: -14px;
    top: 0;
    color: var(--color-gray-600);
  }
}
}

&__link {
  color: var(--color-gray-800);
font-size: 14px;
line-height: 150%;

&:not([href]) {
  color: var(--color-gray-600);
}
}
}
