.categories {
  padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);

  &__title {
    margin-bottom: 40px;
  }
}

.categories-nav {
  margin-bottom: var(--offset-descr);
  display: flex;
  align-items: center;


  @include tablet {
    flex-wrap: wrap;
  }

  &__item {
    width: calc(100% / 6);

    @include tablet {
      width: calc(100% / 3);
    }

    @include mobile {
      width: calc(100% / 2);
    }
  }

  &__btn {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    color: var(--color-gray-600);
    border-radius: 4px;
    border: 1px solid transparent;
    font-size: 16px;
    font-weight: 700;
    line-height: 160%;
    transition: color .3s ease-in-out;

    &--active {
      color: var(--color-primary);
      border-color: currentColor;
    }
  }
}

.categories-list {
  margin-bottom: var(--offset-descr);

  @include mobile {
    grid-template-columns: repeat(6, 1fr);
  }

  &__item {
    grid-column: 6 span;
  }

}

.categories-pag {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;

  &__item {}

  &__link {
    color: var(--colorgray-800);
    font-weight: 700;
    line-height: 160%;
    transition: color .3s ease-in-out;


    svg {
      display: block;
      width: 24px;
      height: 24px;
      fill: currentColor;
      object-fit: cover;
    }

    &--active {
      color: var(--color-primary);
    }
  }
}
