.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 4px;

  @include tablet {
    height: 300px;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    z-index: 10;
    opacity: 0.6;
    background: var(--color-gray-900);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }

  &--active {
    &::before {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__content {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    position: absolute;
    z-index: 100;
    left: 50%;
    top: 50%;
    width: 80px;
    height: 80px;
    border-radius: 100px;
    transform: translate(-50%, -50%);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

    &--active {
      opacity: 0;
      visibility: hidden;
    }


    &:focus {
      outline: 1px solid var(--color-primary);
      outline-offset: 2px;
      }

      svg {
        circle {
          transition: fill .3s ease-in-out;
          &:hover {
            fill:var(--color-active);
        }
        }
      }
}

