.services-section {
  padding-top: 212px;
  padding-bottom: var(--offset-section-middle);
  background-color: var(--color-gray-200);
  background-image: url("../img/services-section-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include mobile {
    padding-top: var(--offset-section-big);
  }

  &__descr {
    margin-bottom: var(--offset-descr);
  }

  &__list {
    margin-bottom: 80px;
  }



  &__subtitle {

  }
}

.services-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;


  &__item {
    grid-column: 1 span;

    @include tablet {
      grid-column: 2 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }
}

.services-item {
  padding: 90px 10px;
  display: block;
  position: relative;
  border-radius: 4px;
  border: 1px solid var(--color-gray-300);
  background: var(--color-light);
  box-shadow: 0px 4px 4px -4px rgba(30, 33, 44, 0.03), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 80px 80px -20px rgba(154, 156, 165, 0.08);

  &--construct {
    &::before {
          background-image: url(../img/services-section-img.jpg);

    }
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: inherit;
    // background-image: url(../img/services-section-img.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0;
    transition: opacity .3s ease-in-out;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 10;
    inset: 0;
    border-radius: inherit;
    background: var(--color-gray-900, 0.6);
    opacity: 0;
    transition: opacity .3s ease-in-out;
  }

  &__content {
    position: relative;
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__icon {
    margin-bottom: 24px;
    width: 52px;
    height: 52px;
    object-fit: cover;
    fill: var(--color-primary);
    transition: fill .3s ease-in-out;
  }

  &__title {
    color: var(--color-gray-900);
    font-size: 20px;
    font-weight: 700;
    line-height: 150%;
    transition: color .3s ease-in-out;

    @include minidesk {
      font-size: 16px;
    }
  }

  &:hover {
    &::after {
      opacity: 0.6;
    }

    &::before {
      opacity: 1;
    }

    .services-item__icon {
    fill: var(--color-light);
    }

    .services-item__title {
      color: var(--color-light);
    }
  }
}
