.news-article {
  height: 100%;
  border-radius: 4px;
  background: var(--color-light);
  box-shadow: 0px 4px 4px -4px rgba(30, 33, 44, 0.03), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 80px 80px -20px rgba(154, 156, 165, 0.08);

  &--big {
    .news-article__content {
      padding: 24px;
    }

    .news-article__title {
      margin-bottom: 12px;
    }

    .news-article__meta {
      margin-bottom: 16px;
    }
  }

  &--small {
    .news-article__content {
      padding: 16px 24px 24px;
    }

    .news-article__title {
      margin-bottom: 8px;
    }

    .news-article__descr {
      display: none;
    }
  }

  &__image {
    @include tablet {
      width: 100%;
      object-fit: cover;
    }
  }

  &__content {}

  &__title {}

  &__link {
    color: inherit;
  }

  &__meta {}

  &__descr {}
}

.article-meta {
  color: var(--color-gray-700);
  font-size: 14px;
  line-height: 150%;

  &__link {
    color: inherit;

    &--active {
      color: var(--color-primary);
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 24px;

      &::after {
        position: absolute;
        content: "";
        top: 2px;
        right: -14px;
        width: 1px;
        height: 12px;
        background-color: var(--color-gray-700);
      }
    }
  }

  &__item--comments {
    padding-left: 24px;
    background-image: url("../img/comments.svg");
    background-position: left center;
    background-size: 16px 16px;
    background-repeat: no-repeat;

  }

}

