.we-offer {
  padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;

    @include tablet {
      display: block;
    }
  }

  &__image {
    grid-column: 7 span;
  }

  &__content {
    grid-column: 5 span;
    padding-left: 75px;

    @include tablet {
     padding: 0;
     padding-top: 20px;
    }
  }

  &__title {
    margin-bottom: var(--offset-descr);
    color: var(--color-gray-900);
    font-family: var(--family-second);
    font-size: 46px;
    font-weight: 900;
    line-height: 130%;
  }

  &__accordion {
    .accordion__control {
      padding-right: 10px;
      padding-left: 44px;
      position: relative;
      text-align: left;
    }

    .accordion__title {
      color: var(--color-gray-900);
      font-size: 25px;
      font-weight: 700;
      line-height: 150%;
    }

    .accordion__content {
      padding: 12px 44px;
      color: var(--color-gray-700);
      line-height: 160%;
    }

    .accordion__icon {
      position: absolute;
      left: 1px;
      top: 10px;
      display: block;
      width: 22px;
      height: 22px;

      &::before {
        position: absolute;
        content: "";
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        height: 2px;
        border-radius: 100px;
        background-color: var(--color-primary);
      }

      &::after {
        position: absolute;
        content: "";
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        width: 2px;
        height: 100%;
        border-radius: 100px;
        background-color: var(--color-primary);
        transition: opacity .3s ease-in-out;
      }
    }
  }

  .is-open .accordion__icon {
    transform: none;

    &::after {
      opacity: 0;
    }
  }
}
