.decisions {
  padding-top: var(--offset-section-middle);
  background-color: var(--color-gray-200);

  .grid {
    @include minitablet {
      display: block;
    }
  }

  &__left {
grid-column: 5 span;
  }

  &__right {
    grid-column: 7 / 6 span;
  }

  &__image {
   margin-bottom: -60px;

   @include minitablet {
 margin-bottom: 20px;
  }
  }

  &__title {
margin-bottom: var(--offset-descr);
  }

  &__list {
display: flex;
flex-direction: column;
gap: 20px;
  }

  &__item {
    position: relative;
    padding-left: 37px;
    color: var(--color-gray-800);
    line-height: 160%;

    &::before {
      position: absolute;
      left: 0;
      top: 3px;
      content: '';
      width: 20px;
      height: 20px;
      background-image: url("../img/decisions-check.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
}
