.footer {
  padding-top: var(--offset-section-middle);
  padding-bottom: var(--offset-section-middle);
  background-color: var(--color-gray-900);
  color: rgba(255, 255, 255, 0.6);


  &__container {}

  &__top {
    margin-bottom: 49px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;

    @include mobile {
      gap: 15px;
    }
  }

  &__left {
    grid-column: 5 span;

    @include minitablet {
      grid-column: 12 span;
    }


    &--wrap {
      display: flex;
      margin-bottom: 24px;

      @include mobile {
      display: block;
      }
    }
  }

  &__logo {
    margin-right: 60px;

    @include mobile {
    display: inline-block;
    margin-right: 0;
    margin-bottom: 20px;
    }
  }

  &__descr {
    font-size: 14px;
    line-height: 150%;
    opacity: 0.6;
  }

  &__title {
    margin-bottom: 13px;
   color: rgba(255, 255, 255, 1);
font-size: 16px;
font-weight: 700;
line-height: 150%;
letter-spacing: 1px;
text-transform: uppercase;

@include mobile {
 font-size: 14px;
}
  }



  &__bottom {
    margin-bottom: 60px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;

    @include mobile {
      gap: 15px;
    }

  }

  &__copyright {
    span {
      padding-left: 20px;
      background-image: url("../img/heart.svg");
      background-position: left center;
      background-repeat: no-repeat;
      background-size: 16px 16px;
    }
  }


}

.social {
  display: flex;
  align-items: center;
  gap: 16px;
}

.footer-form {
  grid-column: 8 / 5 span;

  @include minitablet {
    grid-column: 12 span;
  }

  &__items {
    width: 100%;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
  }

  &__title {
    margin-bottom: 24px;
    font-size: 24px;
    line-height: 150%;
    color: rgba(255, 255, 255, 1);
  }

  &__field {
    width: 100%;
  }

  &__input {
    width: 100%;
    border: 1px solid rgba(255, 255, 255, 0.20);
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

    background: rgba(255, 255, 255, 0.12);
  }

  &__btn {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  &__descr {
    font-size: 12px;
    line-height: 150%;
    opacity: 0.6;
  }
}



.footer-address {
  grid-column: 4 span;

  @include minitablet {
    grid-column: 12 span;
  }

&__title {

}

&__item {
display: block;

&:not(:last-child) {
  margin-bottom: 4px;
}

}

&__name {
  color: rgba(255, 255, 255, 1);
}

&__link {
color: inherit;
}
}

.footer-nav {
  padding-left: 56px;
  grid-column: 8 span;
  display: flex;

  @include minitablet {
    grid-column: 12 span;
    padding-left: 0;
  }
&__col {
&:not(:last-child) {
  margin-right: 124px;

  @include mobile {
    margin-right: 20px;
  }
}
}

&__link {
line-height: 160%;
  color: inherit;
}
}
