:root {
  // base
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  //font
  --family-main: "Ubuntu", sans-serif;
  --family-second: "Lato", sans-serif;

  //color
  --color-light: #fff;
  --color-primary: #FF5A30;
  --color-active: #fc3300;
  --color-gray-900: #1E212C;
  --color-gray-800: #424551;
  --color-gray-700: #787A80;
  --color-gray-600: #9A9CA5;
  --color-gray-400: #D7DADD;
  --color-gray-300: #E5E8ED;
  --color-gray-200: #F4F5F6;

  //offset
  --offset-section-big: 120px;
  --offset-section-middle: 80px;
  --offset-section-small: 60px;
  --offset-descr: 60px;

  --gap: 30px;

  @include minitablet {

    --offset-section-big: 80px;
    --offset-section-middle: 60px;
    --offset-section-small: 40px;
    --offset-descr: 40px;
}

@include tablet {
  --offset-section-big: 50px;
  --offset-section-middle: 40px;
  --offset-section-small: 20px;
  --offset-descr: 20px;
}
}

