
.nav {
&__list {
  display: flex;
  align-items: center;
  gap: 40px;

  @include tablet {
    display: block;
    margin-bottom: 40px;
  }
}

&__item {
  @include tablet {
   margin-right: 0;
   margin-bottom: 30px;
  }
}

&__link {
  font-weight: 700;
line-height: 160%;
color: var(--color-gray-800);
transition: color .3s ease-in-out;

&:focus {
outline: 1px solid var(--color-primary);
outline-offset: 2px;
}

&:hover {
color: var(--color-primary);
outline: none;
}

&:active {
color: var(--color-active);
outline: none;
}

&--current {
  color: var(--color-primary);
}
}
}
