.comment {
display: flex;

@include tablet {
  flex-direction: column;
}

&__left {
  padding-right: 30px;
width: calc(100% - 630px);

@include tablet {
 width: 100%;
 margin-bottom: 20px;
}
}

&__right {
width: 630px;

@include tablet {
 width: 100%;
}
}

&__author {
  display: block;
  margin-bottom: 8px;
  color: var(--color-gray-900);
  font-size: 18px;
  font-weight: 700;
  line-height: 150%;
}

&__time {
  color: var(--color-gray-700);
  font-size: 14px;
  line-height: 150%;
}

&__body {

p {
  margin-bottom: 16px;
  color: var(--color-gray-800);
line-height: 160%;
}

a {
  font-weight: 700;
  color: var(--color-primary);
}
}

&__reply {
  display: inline-flex;
  align-items: center;
  color: var(--color-gray-700);
  font-size: 14px;
  line-height: 150%;

  svg {
    display: block;
    margin-right: 8px;
    width: 16px;
    height: 16px;
    fill: currentColor;
    object-fit: cover;
  }
}

}
