.contacts-info {
  padding-top: var(--offset-section-middle);
  padding-bottom: var(--offset-section-middle);
  background: var(--color-gray-200);

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    margin-bottom: 120px;
    --contacts-gap: 120px;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--contacts-gap) * 2);

    @include middledesk {
      --contacts-gap: 30px;
    }

    @include tablet {
      --contacts-gap: 20px;
    }

    @include minitablet {
      --contacts-gap: 10px;
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile{
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      &::after {
        position: absolute;
        content: "";
        top: 0;
        right: calc(-1 * var(--contacts-gap));
        width: 1px;
        height: 100%;
        background: linear-gradient(315deg, rgba(218, 219, 221, 0.00) 0%, rgba(218, 219, 221, 0.50) 12.33%, #DADBDD 51.91%, rgba(218, 219, 221, 0.50) 87.85%, rgba(218, 219, 221, 0.00) 100%);

        @include minitablet {
          display: none;
        }

      }
    }
  }

  &__subtitle {
    margin-bottom: 24px;
    color: var(--color-gray-900);
    font-size: 28px;
    line-height: 150%;

    @include tablet {
      font-size: 24px;
    }
  }

  &__address {
    margin-bottom: 4px;
    color: var(--color-gray-900);
    line-height: 160%;
  }

  &__link {
    display: inline-block;
    margin-bottom: 20px;
    color: var(--color-primary);
    line-height: 160%;
    text-decoration-line: underline;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  &__element {
    color: var(--color-gray-700);
    line-height: 160%;
  }

  &__value {
    color: var(--color-gray-900);
  }

  &__social {
    display: flex;
    justify-content: center;
    gap: 24px;
  }
}
