.hero {
  // padding-top: 220px;
  // padding-bottom: 120px;
  // background-color: var(--color-gray-800);
  // background-image: url("../img/hero.jpg");
  // background-position: center;
  // background-size: cover;
  // background-repeat: no-repeat;

  &__content {
    max-width: 595px;
  }

  &__title {
    color: var(--color-light);
  }

  &__descr {
    font-weight: 400;
    color: var(--color-light);
  }

  &__btns {
    margin-top: 61px;
  }

  &__btn {
    margin-right: 24px;
    color: var(--color-light);
    outline: 1px solid var(--color-light);

    @include mobile {
      margin-right: 10px;
      margin-bottom: 20px;
    }



    &:focus {
      outline: 1px solid var(--color-primary);
      outline-offset: 3px;
    }

    &:hover {
      background-color: var(--color-active);
      outline-color: var(--color-active);
    }

    &:active {
      background-color: var(--color-active);
    }
  }

  &__next {
    position: absolute;
    top: 50%;
    right: 32px;
    z-index: 100;
    background-color: var(--color-light);
    transform: translateY(-50%);

    @include middledesk {
      display: none;
    }
  }

  &__prev {
    position: absolute;
    top: 50%;
    left: 32px;
    z-index: 100;
    background-color: var(--color-light);
    transform: translateY(-50%);

    @include middledesk {
      display: none;
    }
  }

  &__pag {
    counter-reset: pag;
    margin-bottom: 10px;

    @include mobile {
      display: none;
    }

    .swiper-pagination-bullet {
      width: 30px;
      height: auto;
      text-align: center;
      background-color: transparent;
      color: var(--color-light);
      opacity: 1;
      font-size: 28px;

      font-weight: 700;


      &::before {
        counter-increment: pag;
        content: "0" counter(pag);
      }

    }
  }
}

.swiper-pagination-bullet-active {
  &::before {
    color: var(--color-primary);
  }
}


.hero__slider {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.hero-slide {
  padding-top: 220px;
  padding-bottom: 120px;
  background-color: var(--color-gray-800);
  background-image: url("../img/hero.jpg");
  background-position: center;
  background-size: cover;

  @include mobile {
    padding-top: 80px;
    padding-bottom: 80px;
  }

}

.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 10px;
}
