.contacts {
    padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);

  &__descr {
    margin-bottom: var(--offset-descr);
  }

  &__image {
    margin-right: 20px;

    @include tablet {
      margin: 0;
      margin-bottom: 30px;


    }
  }

  &__content {
    display: flex;
    justify-content: space-between;

    @include tablet {
      display: block;
      text-align: center;
    }
  }
}

.form-field__flex {
  display: flex;
  gap: 24px;
  align-items: center;
}

.message__field {
  width: 100%;
  margin-bottom: 40px;
}



.form-contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 600px;

  @include tablet {
    max-width: 100%;
    text-align: initial;
  }

  &__field {
    margin-bottom: 24px;
    width: 47.5%;

    @include tablet {
      --contacts-gap: 20px;
    }

    @include mobile {
  display: block;
  width: 100%;
    }
  }

  &__fields {
   border: none;
   padding: 0;
   width: 47.5%;

   @include tablet {
  margin-bottom: 24px;
  }

   .form-field__caption {
    margin-bottom: 20px;
   }
  }

  &__input {
    resize: none;
    height: 128px;
    width: 100%;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @include tablet {
    flex-direction: column;
    gap: 20px;
    }
  }

  .custom-checkbox__content {
    max-width: 287px;
  }

}

.choices {
  width: 100%;
}

.is-focused {
  &:focus {
    outline: 1px solid var(--color-primary);
    outline-offset: 2px;
    }
}


.choices__placeholder {

}


.choices__list--single {
  padding: 0;
  display: flex;
  align-items: center;
  height: 100%;
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 0;
}

.choices__list--dropdown .choices__item--selectable:after {
  display: none;
}

.choices__inner {
  padding: 0;
  padding: 12px 16px;
  height: 100%;
  border-radius: 4px;
  border: 1px solid var(--color-gray-400);
  background: var(--color-gray-200);
  color: var(--color-gray-600);
  font-size: 14px;
  line-height: 150%;
  transition: transform .3s ease-in-out;
}

.choices[data-type*=select-one]:after {
  margin-top: 0 !important;
border: none;
width: 14px;
height: 7px;
background-image: url("../img/down-chevron.svg");
background-position: center;
background-size: cover;
background-repeat: no-repeat;
}

.choices[data-type*=select-one].is-open:after {
  transform: rotate(180deg);
}

