.services {
  overflow: hidden;
  position: relative;
  padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);

  &-content-bg {
    &--1 {
      position: absolute;
      left: -6px;
      bottom: -248px;
    }

    &--2 {
      position: absolute;
      right: -8px;
      margin-top: -134px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__list {
    position: relative;
    z-index: 10;
  }

  &__item {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    align-items: center;

    @include minitablet {
      grid-template-columns: repeat(6, 1fr);
      gap: 10px;
    }

    &:not(:last-child) {
      margin-bottom: 120px;

      @include mobile {
        margin-bottom: 40px;
      }
    }

    .services__image {
      @include minitablet {
   text-align: center;
         }
    }

    &--reverse {
      .services__info {
        order: -1;

        @include minitablet {
         order: initial;
        }
      }

      .services__image {
        display: flex;
        justify-content: flex-end;

        @include minitablet {
       display: block;
        }
      }
    }
  }

  &__image {
    grid-column: 6 span;
  }

  &__info {
    padding: 25px;
    grid-column: 6 span;
  }

  &__title {
    margin-bottom: 24px;
    color: var(--color-gray-900);
    font-size: 32px;
    line-height: 150%;
  }

  &__descr {
    margin-bottom: 48px;
  }

  &__link {}


}
