.work-images {
  overflow: hidden;
  margin-top: 60px;

  &-slider {
    position: relative;
    margin-bottom: 20px;
  }

  &__next {
    position: absolute;
    top: 50%;
    right: 32px;
    transform: translateY(-50%);
    z-index: 100;
    background-color: rgba($color: #fff, $alpha: 0.5);
  }

  &__prev {
    position: absolute;
    top: 50%;
    left: 32px;
    transform: translateY(-50%);
    z-index: 100;
    background-color: rgba($color: #fff, $alpha: 0.5);
  }

  .swiper-slide img {
    display: block;
    border-radius: 4px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .slider-nav__btn.swiper-button-disabled {
    opacity: 0.4;
    pointer-events: none;
  }

}


.work-images-nav {
  overflow: hidden;

  .swiper-slide {
    border-radius: 4px;
    opacity: 0.6;
    cursor: pointer;
    transition: opacity .3s ease-in-out;
  }

  .swiper-slide-thumb-active {
    opacity: 1;
  }

  .swiper-slide img {
    display: block;
    border-radius: 4px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
