.portfolio-section {
  padding-top: var(--offset-section-big);
  padding-bottom: var(--offset-section-big);

  &__top {
    margin-bottom: var(--offset-descr);
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    @include mobile {
    display: block;
    }
  }

  &__title {
    margin: 0;
    max-width: 705px;

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__items {
    padding: 80px 10px;
    margin: -80px -10px;
  }

  &__learn {
    margin-top: 80px;
      }
}


