.post {
padding-bottom: var(--offset-section-big);

  &__cover {
    margin-top: -80px;
    display: block;
    width: 100%;
    object-fit: cover;
  }

  &__wrapper {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 840px;
  }

  &__content {
    padding-top: 80px;
    padding-bottom: 60px;

    @include mobile {
      padding-top: 40px;
      padding-bottom: 40px;
    }


    * {
      margin-bottom: 28px;
      font-size: 16px;
      line-height: 160%;
      color: var(--color-gray-800);

      &:last-child {
        margin-bottom: 0;
      }
    }

    h2 {
      margin-bottom: 25px;
      color: var(--color-gray-900);
      font-size: 20px;
      font-weight: 700;
      line-height: 150%;
    }

    h3 {
      margin-bottom: 25px;
      color: var(--color-gray-900);
      font-size: 18px;
      font-weight: 700;
      line-height: 150%;
    }

    p {
      margin-bottom: 28px;
      color: var(--color-gray-800);
      font-size: 16px;
      line-height: 160%;
    }

    blockquote {
      position: relative;
      margin: 0;
      margin-bottom: 28px;
      padding: 21px 58px;
      padding-right: 5px;
      color: var(--color-gray-900);
      font-size: 20px;
      font-weight: 700;
      line-height: 150%;

      &::before {
        position: absolute;
        left: 0;
        top: 20px;
        width: 28px;
        height: 28px;
        content: "";
        background-image: url("../img/braces.svg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
      }

      @include mobile {
        padding: 0;
        padding-top: 50px;
      }
    }

    ul {
      list-style-type: none;
      margin: 0;
      margin-bottom: 30px;
      padding: 0;
      color: var(--color-gray-800);
      line-height: 160%;

      li {
        position: relative;
        padding-left: 32px;

        &:not(:last-child) {
          margin-bottom: 3px;
        }

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 4px;
          width: 16px;
          height: 16px;
          background-image: url("../img/check-list.svg");
          background-position: center;
          background-size: cover;
          background-repeat: no-repeat;
        }
      }
    }



  }

  &__share {
    display: flex;
    align-items: center;
    color: var(--color-gray-900);
    font-weight: 700;
    line-height: 160%;

    span {
      margin-right: 10px;
    }
  }
}
