.know-form {
  position: relative;
top: 92px;
  margin: 0 auto;
  padding: 48px 60px;
  // padding-bottom: 48px;
  border-radius: 4px;
  background: var(--color-light);
  box-shadow: 0px 4px 4px -4px rgba(30, 33, 44, 0.03), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 80px 80px -20px rgba(154, 156, 165, 0.08);

  @include mobile {
    top: initial;
    padding: 20px 0;
  }

  &__title {
    margin-bottom: 32px;
    color: var(--color-gray-900);
    text-align: center;
    font-size: 28px;
    font-weight: 700;
    line-height: 150%;
  }

  &__content {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    gap: 24px;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &__field {
    @include minitablet {
      width: 100%;
    }

    .form-field__input {
      @include minitablet {
        width: 100%;
      }
    }
  }
}
