.page-hero {
  padding-top: 152px;
  padding-bottom: 80px;
  min-height: 600px;
  background-color: var(--color-gray-200);
  background-position: right -8px top;
  background-repeat: no-repeat;

  @include tablet {
    padding-top: 60px;
    min-height: initial;
  }

  &__content {
    max-width: 600px;
  }

  &__breadcrumbs {
    margin-bottom: 40px;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      margin-bottom: 20px;
      flex-direction: column;
      align-items: flex-start;
      gap: 20px;
    }
  }

  &--services {
    background-image: url("../img/page-hero-bg.png");
  }

  &--services-inner {
    background-image: url("../img/page-hero-bg--inner.png");
  }

  &--work {
    background-image: url("../img/work-hero-bg.png");
  }

  &--work-inner {
    background-color: var(--color-light);
    background-image: url("../img/work-inner-hero-bg.png");
  }

  &--about {
    background-color: var(--color-light);
    background-image: url("../img/about-hero-bg.png");
  }

  &--positions {
    background-color: var(--color-light);
    background-image: url("../img/positions-hero-bg.png");
    background-position: bottom right;
  }

  &--news {
    background-image: url("../img/news-hero-bg.png");
    background-position: center right;
  }

  &--contacts {
    background-image: url("../img/contacts-hero-bg.png");
    background-position: center right;
  }

  &--post {
    .page-hero__content {
    max-width: 100%;
    }
}

@include tablet {
  background-image: none;
}
}
