.portfolio-item {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 4px;
  background: var(--color-light);
  box-shadow:
    0px 4px 4px -4px rgba(30, 33, 44, 0.03),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 80px 80px -20px rgba(154, 156, 165, 0.08);


    &:hover {
      .portfolio-item__btn{
        margin-bottom: 0;
      }
    }

  &__image {
    height: 100%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 24px 10px;
  }

  &__title {
margin-bottom: 5px;

@include middledesk{
font-size: 18px;
min-height: 52px;
}


  }

  &__descr {

  }

  &__btn {
    margin-bottom: -75px;
margin-top: 24px;
transition: .3s ease-in-out;

@include minitablet {
  margin-bottom: 0;
}
}
}
