.partners {
  padding-top: var(--offset-section-middle);
  padding-bottom: var(--offset-section-middle);
  background-color: var(--color-gray-200);

  &__descr {
    text-align: center;
    margin-bottom: 32px;
  }


  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 102px;
    row-gap: 40px;

    @include minidesk {
      gap: 20px;
    }
  }

  &__item {
    @include minidesk {
     grid-column: 2 span;
     display: flex;
     justify-content: center;
    }

    @include mobile {
    grid-column: 3 span;
    }
  }

  &--about {
    background-color: var(--color-light);
  }
}
