.slider-nav {
  position: relative;
  z-index: 100;
  display: flex;
  align-items: center;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border-radius: 100%;
    transition: background-color .3s ease-in-out;

    svg {
      width: 24px;
      height: 24px;
      object-fit: cover;
      fill: var(--color-gray-800);
      transition: fill .3s ease-in-out;
    }

    &:hover {
      background-color: var(--color-primary);

      svg {
        fill: var(--color-light);
      }
    }
  }
}

