.facts {
  position: relative;
  overflow: hidden;
  padding-top: 180px;
  padding-bottom: var(--offset-section-small);
  background-color: var(--color-light);

  @include mobile {
    padding-top: var(--offset-section-big);
  }

  &::before {
    position: absolute;
    content: "";
    left: 0;
    top: -195px;
    width: 570px;
    height: 1065px;
    background-image: url("../img/facts-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

  }

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    margin: 0 auto;
    max-width: 736px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 48px;

  }

  &__item {
    @include minitablet {
      grid-column: 2 span;
    }
  }
}

.facts-elem {
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include minitablet {
    justify-content: center;
    align-items: center;
  }

  &__circle {

    position: relative;
    width: 148px;
    height: 148px;

    svg {
      position: absolute;
      inset: 0;
    }
  }

  &__circle {
    position: relative;
  }

  &__value {
    position: absolute;
    top: 50%;
    left: 50%;
    color: var(--color-gray-900);
    font-size: 32px;
    font-weight: 700;
    line-height: 150%;
    transform: translate(-50%, -50%);
  }

  &__text {
    display: block;
    text-align: center;
    color: var(--color-gray-900);



    font-size: 14px;
    line-height: 150%;
  }
}
